html, body {
  font-family: 'Lato', sans-serif;
  color: #1d1d1d;
  padding: 0px;
  margin: 0px;
  width: 100%;
  height: 100%;
  scroll-behavior: smooth;
}

p {
font-size: 16px;
line-height: 22px;
margin-bottom: 22px;
letter-spacing: 0.2px;
}

.monospace {
  font-family: 'IBM Plex Mono', monospace;
}

.table {
  display: table;
  width: 100%;
}

.cell {
  display: table-cell;
  vertical-align: middle;
}

.container {
width: 100%;
height: 100%;
overflow-x: hidden;
overflow-y: auto;
-webkit-overflow-scrolling: touch;
scroll-behavior: smooth;
}

.clearfix:after {
content: " "; /* Older browser do not support empty content */
visibility: hidden;
display: block;
height: 0;
clear: both;
}

.hero {
  background-image: linear-gradient(to top, #7A8F88 0%, #C5D0B7 100%);
  color: white;
  background-attachment: fixed;
background-position: center;
background-repeat: no-repeat;
background-size:cover;
  position: relative;
}

.hero p {
  font-size: 18px;
  letter-spacing: 0.5px;
line-height: 24px;
}

.hero .table {
  min-height: 100vh;
}

.hero .table .cell {
  padding-top: 60px;
  padding-bottom: 60px;
  text-align: center;
}

.hero h1 {
  font-size: 100px;
  line-height: 100px;
  margin-top: 0px;
  margin-bottom: 20px;
font-weight: 600;
}

.hero .button {
  margin-top: 20px;
}

.modal-title {
  margin-top: -30px;
  margin-bottom: 8px;
  padding-right: 100px;
  letter-spacing: 1px;
}

.button {
  cursor: pointer;
  height: 56px;
  line-height: 54px;
  padding-left: 24px;
  padding-right: 24px;
  box-sizing: border-box;
  display: inline-block;
  border-radius: 4px;
  text-align: center;
  text-decoration: none;
  outline: none;
  vertical-align: middle;
  font-weight: bold;
  transition: all 250ms ease-in-out;
  border: solid 1px #f37f47;
  font-size: 12px;
  letter-spacing: 2px;
  text-transform: uppercase;

  background-color: #f37f47;
  color: white;
}

.button.white {
  background-color: rgba(255,255,255,1);
  border: solid 1px white;
}

.button.hollow {
  background-color: rgba(255,255,255,0);
  color: white;
  border: solid 1px white;
}

.button.small {
  height: 44px;
  line-height: 42px;
  padding-left: 16px;
  padding-right: 16px;
}

.button.green {
background: #9ABAAF;
color: white;
border-color: #9ABAAF;
}

.button.gradient {
  background-size: 200%;
  background-position: 0% 0%;
  background-image: linear-gradient(to right, #ff9a9e 0%, #fecfef 99%, #fecfef 100%);
  color: white;
  border: none;
  background-color: transparent;
  line-height: 56px;
}

.button.gradient.small {
  line-height: 44px;
}

.button.icon i {
  margin-right: 8px;
}

.modally-wrap.open:not(.last) {
  filter: blur(5px);
}

.code-wrap {
position: absolute;
top: 16px;
left: 16px;
width: calc(100% - 32px);
text-align: left;
}

.code-button {
height: 30px;
display: inline-block;
background: rgba(255,255,255,1);
border-radius: 15px;
color: black;
text-decoration: none;
text-transform: uppercase;
font-family: 'IBM Plex Mono', monospace;
padding-left: 5px;
padding-right: 10px;
line-height: 30px;
font-weight: 600;
vertical-align: top;
transition: all 250ms ease-in-out;
border: solid 1px white;
font-size: 14px;
}

.code-button img {
height: 100%;
margin-right: 5px;
vertical-align: top;
transition: all 250ms ease-in-out;
}

.code-button.black {
background: black;
border-color: black;
color: white;
}

.code-button.black img {
filter: invert(1);
}

.code-button .show {
display: inline;
}

.code-button .hide {
display: none;
}

.code-button.active .show {
display: none;
}

.code-button.active .hide {
display: inline;
}

.code-wrap .code {
display: block;
position: absolute;
top: 46px;
left: 0px;
width: 100%;
background: white;
max-height: calc(100vh - 118px);
overflow: auto;
-webkit-overflow-scrolling: touch;
color: black;
box-sizing: border-box;
padding: 16px;
display: none;
box-shadow: 0px 0px 5px rgba(0,0,0,0.2);
border: 1px solid #ccc;
}

@media screen and (min-width: 960px) {
.columns {
margin-left: -8px;
margin-right: -8px;
}
.columns .column {
width: calc(50% - 16px);
padding-left: 8px;
padding-right: 8px;
float: left;
}

.glide__bullets {
  display: none !important;
}

body.editor-active .container main > .content  {
margin-left: 500px;
}

  .button:hover {
      background-color: white;
      color: #f37f47;
  }

  .button.white:hover {
      background-color: rgba(255,255,255,0);
      color: white;
  }

  .button.hollow:hover {
      background-color: #f37f47;
      color: white;
border-color: #f37f47;
  }

  .button.gradient:hover {
      background-color: transparent;
      background-position: 50% 0px;
  }

.button.green:hover {
background: white;
color: #9ABAAF;
}

.code-button:hover {
background: rgba(255,255,255,0);
color: white;
}

.code-button:hover img {
filter: invert(1);
}

.code-button.black:hover {
background: white;
color: black;
}

.code-button.black:hover img {
filter: invert(0);
}
}

.center {
max-width: 1440px;
margin-left: auto;
margin-right: auto;
box-sizing: border-box;
padding-left: 56px;
padding-right: 56px;
}

.center.p656 {
max-width: 656px;
box-sizing: content-box;
}

section.carousel, section.text {
position: relative;
padding-top: 60px;
padding-bottom: 60px;
}

footer {
background: #595959;
margin-top: 60px;
}

blockquote {
font-size: 24px;
font-weight: 300;
line-height: 32px;
letter-spacing: 0.5px;
margin-top: 30px;
margin-bottom: 30px;
}

@media screen and (max-width: 959px) {
.hero .table {
min-height: 500px;
}

blockquote {
font-size: 20px;
font-weight: 300;
line-height: 28px;
letter-spacing: 0.3px;

margin-top: 20px;
margin-bottom: 20px;
}

.hero p {
font-size: 16px;
line-height: 20px;
}

p {
font-size: 14px;
line-height: 20px;
margin-bottom: 20px;
}

.center {
padding-left: 16px;
padding-right: 16px;
}

  .hero h1 {
      font-size: 32px;
      line-height: 50px;
      letter-spacing: 5px;
  }

  .button {
      height: 44px;
      line-height: 42px;
      padding-left: 16px;
      padding-right: 16px;
  }

  .button.gradient {
      line-height: 44px;
  }

section.carousel, section.text {
padding-top: 30px;
padding-bottom: 30px;
}

footer {
margin-top: 30px;
}
}

.modally .button-wrap {
  text-align: right;
}

.modally .button-wrap .button {
  margin-left: 16px;
}

.github-buttons {
  position: absolute;
  top: 0px;
  right: 0px;
  padding: 16px;
}

.down {
  text-align: center;
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100%;
  line-height: 50px;
  font-size: 24px;
  color: white;
}

.down span {
  display: inline-block;
}

.sidebar {
position: fixed;
z-index: 99999;
max-width: 500px;
background: white;
width: calc(100% - 40px);
height: 100%;
top: 0px;
left: 0px;
transition: all 250ms ease-in-out;
transform: translateX(-100%);
}

.sidebar #editor {
height: 100%;
}

.sidebar #editor .editor {
padding-top: 66px;
}

.sidebar #editor .ql-toolbar {
z-index: 1;
position: absolute;
width: 100%;
top: 0px;
left: 0px;
}

.container main > .content {
transition: all 250ms ease-in-out;
}

.sidebar .carret {
position: absolute;
right: -39px;
bottom: 0px;
width: 40px;
text-align: center;
height: 40px;
background: white;
border-top: 1px solid #ccc;
border-right: 1px solid #ccc;
}

.sidebar .carret a {
display: block;
width: 40px;
height: 40px;
}

.sidebar .carret img {
vertical-align: middle;
height: 100%;
transition: all 250ms ease-in-out;
}

body.editor-active .sidebar .carret img {
transform: rotate(180deg);
}

body.editor-active .sidebar {
transform: translateX(0px);
}

.spacer {
display: block;
width: 100%;
visibility: hidden;
}

section.carousel .carousel-item {
padding-left: 8px;
padding-right: 8px;
}

section.carousel .carousel-item .image {
background-size: cover;
background-position: center;
background-repeat: no-repeat;
}

section.carousel .slider {
margin-left: -8px;
margin-right: -8px;
}

section.carousel .carousel-item .title {
margin-bottom: 10px;
}

section.carousel .carousel-item .text p {
margin-top: 0px;
color: #595959;
}

section.carousel .carousel-item {
padding-bottom: 10px;
}

section.text .text-landing > p > img {
width: 100%;
}

section.text .text-landing p {
color: #595959;
}

section.hero .link-landing {
  display: flex;
  flex-flow: row;
  justify-content: center;
}

.flex-button {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;

  svg {
    display: block;
    margin-top: -2px;
    margin-right: 8px;
  }
}

@import 'glade';

@media screen and (min-width: 960px) {
  .glide__bullets {
    display: none;
  }

  .glide__bullet {
    &:hover {
      border: 0;
      background: rgba(0,0,0,0.4);
    }
  }
  
}  


.glide__bullets {
  position: static;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: translateX(0);
  margin-top: 16px;
}

.glide__bullet.glide__bullet--active {
  background: #9ABAAF !important;
}

.glide__bullet {
  border: 0;
  background: rgba(0,0,0,0.2);
  margin-left: 8px;
  margin-right: 8px;

  &:hover {
    border: 0;
    background: rgba(0,0,0,0.2);
  }
}
